<template>
  <div>
    外卖详情
    <v-back></v-back>
    <v-search></v-search>
    <div class="box" v-if="detail">
      <h3>商品详情</h3>
      <p>菜名：{{detail.name}}</p>
      <p v-if="detail.price">价格：{{detail.price | priceFilter}}</p>
    </div>
    <hr>
    <h3>猜你喜欢</h3>
    <router-link to="/foodDetail/1">麻辣烫</router-link>
    <router-link to="/foodDetail/2">麻辣香锅</router-link>
  </div>
</template>
<script>
  import {foodDetail} from "../util/request"
  import vBack from '../common/vBack.vue'
  export default {
    // 组件更新之前
    beforeRouteUpdate(to, from, next) {
      console.log("组价更新了");
      console.log(to);
      next()
      foodDetail(this.$route.params.id).then(res => {
        console.log(res);
        this.detail = res.data.detail
      })
    },
    components: {
      vBack
    },
    data() {
      return {
        detail: {}
      }
    },
    methods: {},
    watch: {},
    mounted() {
      console.log(this);
      console.log(this.$route.params.id);
      foodDetail(this.$route.params.id).then(res => {
        console.log(res);
        this.detail = res.data.detail
      })
    }
  }

</script>
<style scoped>
  .box {
    width: 80%;
    border: 1px solid red;
    padding: 20px;
    margin: 0 auto;
  }

</style>



